import './profile.css';
import { Topbar } from '../../components/topbar/Topbar';
import { Sidebar } from '../../components/sidebar/Sidebar';
import { Feed } from '../../components/feed/Feed';
import { Rightbar } from '../../components/rightbar/Rightbar';
import { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router';

export const Profile = () => {
  const HEROKU = process.env.REACT_APP_PUBLIC_FOLDER_HEROKU;
  const PF_AWS = process.env.REACT_APP_PUBLIC_FOLDER_AWS;
  const [user, setUser] = useState({});
  const username = useParams().username;

  useEffect(() => {
    const fetchUser = async () => {
      const res = await axios.get(`${HEROKU}users?username=${username}`);

      setUser(res.data);
    };
    fetchUser();
  }, [username, HEROKU]);

  return (
    <>
      <Topbar />
      <div className="profile">
        <Sidebar />
        <div className="profileRight">
          <div className="profileRightTop">
            <div className="profileCover">
              <img
                className="profileCoverImg"
                src={
                  user.coverPicture
                    ? PF_AWS + user.coverPicture
                    : PF_AWS + 'person/noCover.png'
                }
                alt=""
              />
              <img
                className="profileUserImg"
                src={
                  user.profilePicture
                    ? PF_AWS + user.profilePicture
                    : PF_AWS + 'person/noAvatar.png'
                }
                alt=""
              />
            </div>
            <div className="profileInfo">
              <h4 className="profileInfoName">{user.username}</h4>
              <span className="profileInfoDesc">{user.desc}</span>
            </div>
          </div>
          <div className="profileRightBottom">
            <Feed username={username} />
            <Rightbar user={user} />
          </div>
        </div>
      </div>
    </>
  );
};
